import { PageHeaderService } from '@aca-new/app/shared/components/page-header/shared/service/page-header.service';
import { Component, ChangeDetectionStrategy, Input } from '@angular/core';
import { EQimaIconName, EQimaInputSize } from '@qima/ngx-qima';

@Component({
  selector: 'app-page-header',
  templateUrl: './page-header.component.html',
  styleUrls: ['./page-header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PageHeaderComponent {
  /**
   * @description
   * Set the title
   * @type {string}
   * @default ''
   */
  @Input('pageHeaderTitle')
  public title: string = '';

  public searchIcon: EQimaIconName = EQimaIconName.SEARCH;
  public searchSize: EQimaInputSize = EQimaInputSize.LARGE;
  public searchValue: string = '';

  public constructor(private readonly _pageHeaderService: PageHeaderService) {}

  public onSearchClear(): void {
    this.searchTableData();
  }

  public onEnter(): void {
    this.searchTableData();
  }

  public searchTableData(): void {
    this._pageHeaderService.updateSearchValue(this.searchValue);
  }
}
